.pf-links-repeater {
	@each $classname, $color in $local-colors {
		&.highlight-color-#{$classname} ul li a:hover {
			@include highlight($color);
			background-size: 1em auto;
		}
	}

	ul {
		margin: 0;
		padding: 0;

		li {
			list-style: none;
			margin: 0 0 20px;
			padding: 0;
		}
	}
}
